	module.exports = {"data":{"markdownRemark":{"html":"<p>Docker is an open-source software aimed at facilitating application deployment. It offers the possibility to build containers, including an operating system,\nlibraries and all you need to run your application. Therefore, your application can then be deployed on any machine. Its lightweight structure enables you to\nrun several containers on the same machine. A container image is a lightwieght, stand-alone, execuateble package of a piece of software that includes everything\nneeded to run it.</p>\n<h3>Features</h3>\n<p>Docker containers running on a single machine share that machine’s operating system kernel; they start instantly and use less compute and RAM. Images are\nconstructed from filesystem layers and share common files. This minimizes disk usage and image downloads are much faster.</p>\n<p>Docker containers are based on open standards and run on all major *nix distributions, Microsoft Windows, and on any infrastructure including VMs, bare-metal\nand in the cloud.</p>\n<p>Docker containers isolate applications from one another and from the underlying infrastructure. Docker provides the strongest default isolation to limit app\nissues to a single container instead of the entire machine.</p>\n<h2>Overview</h2>\n<p>A container image is a lightweight, stand-alone, executable package of a piece of software that includes everything needed to run it: code, runtime, system tools, system libraries, settings. Available for both Linux and Windows based apps, containerized software will always run the same, regardless of the environment. Containers isolate software from its surroundings, for example differences between development and staging environments and help reduce conflicts between teams running different software on the same infrastructure.</p>\n<h3>See also</h3>\n<ul>\n<li>Docker Compose : to create and manage several containers at the same time.</li>\n</ul>\n<h3>Container Vs. Virtual Machines</h3>\n<ul>\n<li>Container virtualises the Operating System and more portable, where as VMs virtualises the Hardware. </li>\n<li>Container are an abstraction at the app layer that packages code and dependencies togehter. VMs are an abstraction of physical hardware turning one server into many. Hypervisor helps VM to do so.</li>\n<li><a href=\"https://blog.docker.com/2016/03/containers-are-not-vms/\">CONTAINERS ARE NOT VMS</a></li>\n</ul>\n<h3>Installation of Docker</h3>\n<p>Docker is available in two editions: Community Edition (CE) and Enterprise Edition (EE).</p>\n<p><a href=\"https://docs.docker.com/engine/installation/\">Install it from here</a></p>\n<p>Once installed try this</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ docker run hello-world\n\nHello from Docker!</code></pre>\n      </div>\n<p>This message shows that your installation appears to be working correctly.</p>\n<h3>Where to use Docker</h3>\n<ul>\n<li>\n<p><a href=\"https://www.airpair.com/docker/posts/8-proven-real-world-ways-to-use-docker\">8 Proven Real-World Ways to Use Docker</a></p>\n</li>\n<li>\n<p><a href=\"https://www.ctl.io/developers/blog/post/what-is-docker-and-when-to-use-it/\">What is Docker and When to Use It</a></p>\n</li>\n</ul>\n<h3>Docker Tutorials</h3>\n<ul>\n<li>\n<p><a href=\"https://docs.docker.com/get-started/\">Documentation</a></p>\n</li>\n<li>\n<p><a href=\"https://docker-curriculum.com/\">Docker for beginners</a></p>\n</li>\n<li>\n<p><a href=\"https://hackr.io/tutorials/learn-docker\">Docker Tutorials and Courses</a></p>\n</li>\n<li>\n<p><a href=\"https://training.docker.com/\">Docker Training. Learn Docker From Docker. Official Docker Training.</a></p>\n</li>\n</ul>\n<h4>More Information:</h4>\n<p>You can find plenty of informations in the following sites : </p>\n<ul>\n<li><a href='https://www.docker.com/' target='_blank' rel='nofollow'>Docker website</a></li>\n<li><a href='https://docs.docker.com/' target='_blank' rel='nofollow'>Docker docs</a></li>\n<li><a href='https://hub.docker.com/' target='_blank' rel='nofollow'>DockerHub</a></li>\n</ul>","fields":{"slug":"/developer-tools/docker/"},"frontmatter":{"title":"Docker"}}},"pathContext":{"slug":"/developer-tools/docker/"}}

